<template>
  <div class="nav">
    <van-nav-bar
      title="确认订单"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />

    <van-card
      v-for="item in data"
      :key="item.id"
      :num="item.amount"
      :price="item.price"
      desc="描述信息"
      :title="item.product.name"
      :thumb="item.product.coverImage"
      @click-thumb="toDetail(item)"
    />
    <van-dropdown-menu>
      <van-dropdown-item v-model="value1" :options="option1" />
    </van-dropdown-menu>
    <van-dropdown-menu>
      <van-dropdown-item
        v-model="value2"
        :options="option2"
        @change="onchange"
      />
    </van-dropdown-menu>

    <van-submit-bar :price="sum - this.data.bbb" button-text="提交订单" />
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: [],
      value1: 0,
      option1: [
        { text: "河南省郑州市中原区枫杨街道莲花街55号威科姆", value: 0 },
        { text: "常用地址一", value: 1 },
        { text: "常用地址二", value: 2 },
      ],
      value2: 0,
      option2: [
        { text: "选择使用优惠券", value: 0 },
        { text: "满100减10", value: 1, max: 100, min: 10 },
        { text: "满300减30", value: 2, max: 300, min: 30 },
        { text: "满500减50", value: 3, max: 500, min: 50 },
        { text: "满1000减100", value: 4, max: 1000, min: 100 },
        { text: "满2000减200", value: 5, max: 2000, min: 200 },
        { text: "满5000减700", value: 6, max: 5000, min: 700 },
      ],
      aaa: 0,
    };
  },
  //获取购物车页面传递过来的数据
  created() {
    this.data = this.$route.query.dataName;
  },

  methods: {
    //计算优惠券是否达到使用条件
    onchange(i) {
      if (this.option2[i].max <= this.data.aaa) {
        console.log("可用");
        this.data.bbb = this.option2[i].min * 100;
      } else {
        this.$toast("未达到满减金额，不可用");
      }
    },
    onClickLeft() {
      this.$router.go(-1);
    },

    toDetail(item) {
      this.$router.push({
        name: "detail",
        params: {
          id: item.product.id,
        },
      });
    },
  },
  computed: {
    sum() {
      var total1 = 0;
      //累加计算 checkList 数组的每一项的 价格*数量
      this.data.forEach((item) => {
        total1 += item.price * item.amount;
      });
      this.data.aaa = total1;
      this.data.bbb = 0;
      return total1 * 100;
    },
  },
};
</script>

<style></style>
